export default {
	data() {
		return {
			bookTitle:'',
			titleMsg: '请先扫码',
			isbn: ''
		}
	},
	methods: {
		gotoFind(){
			uni.navigateTo({
				url: '/pages/scancode/find'
			})
		},
		scanCode: function(){
			let that = this;
			//判断是否在微信浏览器中
			if(this.$jswx){
				this.$jswx.scanQRCode({
				needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
				scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
				success: function (res) {
				var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
				}
				});
			}
			else {
				uni.scanCode({
					scanType: 'barCode',
					success: function (res) {
						
						console.log('条码类型：' + res.scanType);
						console.log('条码内容：' + res.result);
						
						that.isbn = res.result
						
						uni.request({
							url: 'https://wx.s2.hiiyun.com/apis/book', 
							method: 'POST',
							data: {
								i:res.result  //'978751430687'
							},
							header: {
								'Content-Type': 'application/x-www-form-urlencoded'
							},
							success: (res) => {
								if(res.data.status==200){
									console.log(that.bookTitle)
									console.log(res.data);
									that.bookTitle = res.data.data[0].title
								}
								else {
									that.titleMsg = res.data.data.msg
								}
								
							}
						});
					},
					fail: function(res) {
						that.titleMsg = '连接后台失败'
						console.log(res)
					}
				});				
			}
			
		}
	},
	onLoad() {
		
	}
}
